export class SelectGroupEvalInfoBean{
    handleFlag:string = '';//                    	操作标志
    groupId:string = '';//                    	群号
    groupName:string = '';//                    	群名称
    custNo:string = '';//                    	客户号
    sysCreditEval:string = '';//                    	系统信用评价
    pageNum:number = 1;//                    	当前页数
    pageSize:number = 10;//                    	每页条数
    evalList: Array<evalList>=[];//第三方评价列表List（Map）
}
export class evalList{//第三方评价里list对象
    partyId: string='';//当事人标识/客户号
    partyName: string='';//当事人姓名
    sysCreditEval: string='';//系统信用评价,评价人客户号
    evalName: string='';//评价人姓名
    creditEval: string='';//信用评价
    creditEvalCause: string='';//信用评价原因
    badHabits: string='';//不良嗜好
    badHabitsCause: string='';//不良嗜好原因
    sourceOfIncome: string='';//收入来源
    incomeLevel: string='';//家庭年收入级别
    carFlag: string='';//车辆状况
    familyStableFlag: string='';//家庭关系是否稳定
    healthFlag: string='';//健康状况
    offerLines: string='';//建议额度
    loanOfficerAdvises: string='';//信贷员建议
    sysTargetCustFlag: string='';//系统是否目标授信客户String(1)
    targetCustFlag: string='';//是否目标授信客户 string(1)
    creditFlag: string='';//是否预授信 string(1)
    reason: string='';//修改原因
    remark: string='';//授信备注
    fromDate: string='';//开始日期Date
    thruDate: string='';//结束日期Date
}