import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { API } from './perssion-manage.http.constant';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_COM1, TZB_COM_TOKEN, TZB_TREE, TZB_HTTP_CUS1, TZB_HTTP_COM, } from 'app/@uisftech/common/service/http.constant';
/**
 * 产品
 * 模块下的接口服务
 */
@Injectable()
export class PerssionManageHttp {
    constructor(public httpService: HttpService) {
    }

    queryBusinessWebAppSource(params) {//模板编号查询
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.queryBusinessWebAppSource, params)
    }
   
    createBusinessWebAppSource(params){
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.createBusinessWebAppSource, params)
    }
    modifyBusinessWebAppSource(params){
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.modifyBusinessWebAppSource, params)
    }
    deleteBusinessWebAppSource(params){
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.deleteBusinessWebAppSource, params)
    }
    createPermissions(params) {
        // params['qwe '] = API.createPermissions;
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.createPermissions, params)
    }
    //修改权限
    modifyPermissions(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.modifyPermissions, params)
    }
    //删除权限
    deletePermissions(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.deletePermissions, params)
    }
    //权限查询
    querySecurityPermissionList(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_COM + API.querySecurityPermissionList, params)
    }
}