import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'back-home-page',
  templateUrl: './back-home-page.component.html',
  styleUrls: ['./back-home-page.component.scss']
})
export class BackHomePageComponent implements OnInit {

  flag:boolean = false;
  url:string;

  constructor(
    public router: Router,
  ) { }

  ngOnInit() {
    this.main();
  }

  main(){
    this.url = location.href;
    var tempArray = this.url.split('/');
    if(tempArray[tempArray.length-1] === 'my-custom-group'){
      this.flag = false;
    }else{
      this.flag = true;
    }
  }

  toHomePage(){
    this.router.navigate(['pages/tzb/custom/custom-group/my-custom-group']);
  }

}
