import { EventRespRuleBean } from "./event-resp-rule.bean";

export class EventDefinitionBean {



    eventId: string;//事件ID
    eventType: string;//事件类型
    eventLevel: string;//事件级别
    eventName: string;//事件名称
    eventDescribe: string;//事件描述
    eventStartTime: string;//事件开始时间
    eventEndTime: string;//事件结束时间
    status: string;//状态
    eventSource: string;//事件来源
    sourceChannel: string;//来源渠道

    triggerWay: string;//触发方式



    countNum: number;//	每页显示记录条数
    releaseOrg: string;//	营销活动发布机构

    userId: string;//	员工编号



    firstExecutionTime: string;//首次执行时间
    executionCycleUnit: string;//执行周期单位
    executionCycle: string;// 执行周期
    processingMode: string;//处理模式

    searchStrategy: string;//搜索策略   服务方法 策略

    searchStrategyName: string;//搜索策略   服务方法 策略

    searchService: string;//搜索服务  处理服务

    objectType: string;//对象类型
    objectNo: string;//对象编号

    objectName: string;//对象名称



    createdPersonId: string;//创建人ID
    createdPersonName: string;//创建人名称
    createdPersonOrg: string;//创建人所属机构


    pageNum: number = 1;            //当前页
    pageSize: number = 10;           //每页显示数量


    eventResponseRuleList: any = []; //响应规则集合




    lastUpdatedPerson: string;//最后修改人
    lastUpdatedStamp: string;//最后修改时间

    sourceChannelDesc: string;//来源渠道




}