// 审批实例类型
export class ProcessInstanceModel {
  startTime: number;               // 开始日期
  createTime: number;              // 创建流程日期
  updateTime: number;              // 更新流程日期
  estEndDate: number;              // 超时时间
  endTime: number;                 // 截止日期
  id: number;
  isTimeout: number;               // 是否超时
  systemCode: number;              // 系统编码
  joinUserIds: string;             // 流程审批人员列表
  proccessName: string;            // 流程名称
  processCode: string;             // 流程编码
  customSystemCode: string;        // 截取后的流程编码
  processId: string;               // 流程实例Id
  processDesc: string;            // 流程实例简述
  wfDesc: string;                 // 流程备注
  customStartUserId: string;       // 自定义发起人ID(姓名)
  startUserId: string;             // 流程发起人Id
  startUserIdName: string;        // 流程发起人姓名
  workState: string;               // 流程运行状态
  bigClassName: string;            // 大类名称
  bigClassNo: string;              // 编码
  littleClassNo: string;           // 小类编码
  littleClassName: string;         // 小类名称
  endType: string;                 // 流程结束类型
  orgObj: string;	                 // 流程发起人机构
  orgId: string;	                 // 机构ID
  customOrgObj: string;		         // 自定义机构名称
  orgName: string;		             // 机构名称
  startRow: string;                // 起始行数（非第N页）
  rowNum: string;                  // 每页显示多少条
}

