  id: number;
  systemCode: string;           // 系统分类编码
  processCode: string;          // 流程编码
  customSystemCode: string;     // 截取后的的流程编码
  processId: string;            // 当前流程实例ID
  taskActorsData: object;       // 当前任务成员信息
  taskActors: string;           // 当前任务成员
  taskActorsName: string;        // 当前待处理人员姓名
  taskDefId: string;            // 当前任务节点定义ID
